import React, { useEffect } from "react"
import * as style from "../../styles/Feedback.module.scss"
import * as globalStyle from "../../styles/global.module.scss"
import Tooltip, { onShowTooltip } from "./Tooltip"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faThumbsUp, faPenToSquare } from "@fortawesome/free-solid-svg-icons"

export default function Feedback(props: any) {
    const { codes, tabNames, starredCodes, downloads } = props

    useEffect(() => {
        const thumbsUp = document.getElementById(style.thumbsUp)
        const review = document.getElementById(style.review)
        thumbsUp.addEventListener("mouseenter", () => onShowTooltip(style.thumbsUp))
        review.addEventListener("mouseenter", () => onShowTooltip(style.review))
        return () => {
            thumbsUp.removeEventListener("mouseenter", () => onShowTooltip(style.thumbsUp))
            review.removeEventListener("mouseenter", () => onShowTooltip(style.review))
        }
    }, [])

    let isLiked = false
    function onThumbUoClick() {
        const thumbsUp = document.getElementById(style.thumbsUp)
        const thumbTooltip = document.getElementById("thumbUpTooltip")
        if (isLiked) {
            thumbsUp.style.color = `${globalStyle.toolColor}`
            isLiked = false
        } else {
            thumbsUp.style.color = `${globalStyle.primaryColor}`
            isLiked = true
            window.dispatchEvent(new CustomEvent("mavQ:alert", { detail: { message: "Thank you for your feedback!" } }))
        }
    }

    function onReviewClick() {
        window.dispatchEvent(new Event("mavQ:toggleReviewForm"))
    }

    return (
        <div id={style.feedback}>
            <div className={globalStyle.hasTooltip}>
                <FontAwesomeIcon icon={faThumbsUp} id={style.thumbsUp} className={style.feedbackIcon} onClick={onThumbUoClick} />
                <Tooltip elementId={style.thumbsUp} content="Thumbs up" />
            </div>
            <div className={globalStyle.hasTooltip}>
                <FontAwesomeIcon icon={faPenToSquare} id={style.review} className={style.feedbackIcon} onClick={onReviewClick} />
                <Tooltip elementId={style.review} content="Leave a review" />
            </div>
        </div>
    )
}